<template>
  <div class="login">

    <van-nav-bar title="会员登录" left-arrow @click-left="$router.go(-1)" />

    <div class="container">
      <div class="title">
        <h3>手机号登录</h3>
        <p>未注册的手机号登录后将自动注册</p>
      </div>

      <div class="form">
        <div class="form-item">
          <input v-model="phoneNum" class="inp" maxlength="11" placeholder="请输入手机号码" type="text">
        </div>
        <div class="form-item">
          <input v-model="picCode" class="inp" maxlength="5" placeholder="请输入图形验证码" type="text">
          <img v-if="picUrl" :src="picUrl" @click="getPicCode" alt="">
        </div>
        <div class="form-item">
          <input v-model="msgCode" class="inp" placeholder="请输入短信验证码" type="text">
          <button @click="getCode">{{ second===totalsecond?'获取验证码':second+'秒后重新发送' }}</button>
        </div>
      </div>

      <div class="login-btn" @click="login" >登录</div>
    </div>
  </div>
</template>

<script>
// 导入api下面的请求接口文档内的函数
import { getPicCode, getMsgCode, codeLogin } from '@/api/login'
import { Toast } from 'vant'
export default {
  name: 'LoginIndex',
  data () {
    return {
      picCode: '',
      picKey: '',
      picUrl: '',
      totalsecond: 60, // 总秒数
      second: 60, // 当前时间的秒数
      timer: null, // 定时器id
      phoneNum: '',
      msgCode: ''
    }
  },
  async created () {
    // 测试代码，测试接口是否可以正常使用，正常接收到到传回来的图片信息
    // 注意：但是下面一行内容与methods里定义的getPicCode()方法相同，不需要多次调用
    // const result = await getPicCode()
    // console.log(result)
    this.getPicCode()
  },
  methods: {
    async getPicCode () {
      const { data: { base64, key } } = await getPicCode()
      this.picUrl = base64
      this.picKey = key
      // Toast('图形验证码获取成功~')
    },
    // 校验输入框内容，返回值为false
    validFn () {
      if (!/^1[3-9]\d{9}$/.test(this.phoneNum)) {
        Toast('请输入正确的手机号')
        return false
      }
      if (!/^\w{4}$/.test(this.picCode)) {
        Toast('请输入正确的图形验证码')
        return false
      }
      return true
    },
    async getCode () {
      if (!this.validFn()) {
        return
      }
      if (!this.timer && this.second === this.totalsecond) {
        await getMsgCode(this.picCode, this.picKey, this.phoneNum)
        Toast('短信发送成功，注意查收')
        // 短信发送成功后，开启倒计时
        this.timer = setInterval(() => {
          this.second--

          if (this.second < 1) {
            clearInterval(this.timer) // 关闭当前定时器
            this.second = this.totalsecond
            this.timer = null // 重置定时器
          }
        }, 1000)
      }
    },
    async login () {
      if (!this.validFn()) {
        return
      }
      if (!/^\d{6}$/.test(this.msgCode)) {
        Toast('请输入正确的短信验证码')
        return
      }
      const result = await codeLogin(this.phoneNum, this.msgCode)

      // vuex同步修改传输回来result中的的token和userId信息
      this.$store.commit('user/setUserInfo', result.data) // result.data仅包含token、id等信息！！！！
      console.log(result)
      Toast('登录成功')
      // 跳转页面，分两种情况，如果当前路径包含query的回跳url，说明是从其他页面拦截登录过来的，登陆完需要回跳回去
      // 如果不包含，则正常跳转到首页'/'
      const url = this.$route.query.backurl
      this.$router.replace(url || '/')
    }
  },
  // 离开页面时，需要的操作
  destroyed () {
    clearInterval(this.timer) // 离开页面销毁定时器
  }
}
</script>

<style lang="less" scoped>
.container {
  padding: 49px 29px;

  .title {
    margin-bottom: 20px;
    h3 {
      font-size: 26px;
      font-weight: normal;
    }
    p {
      line-height: 40px;
      font-size: 14px;
      color: #b8b8b8;
    }
  }

  .form-item {
    border-bottom: 1px solid #f3f1f2;
    padding: 8px;
    margin-bottom: 14px;
    display: flex;
    align-items: center;
    .inp {
      display: block;
      border: none;
      outline: none;
      height: 32px;
      font-size: 14px;
      flex: 1;
    }
    img {
      width: 94px;
      height: 31px;
    }
    button {
      height: 31px;
      border: none;
      font-size: 13px;
      color: #cea26a;
      background-color: transparent;
      padding-right: 9px;
    }
  }

  .login-btn {
    width: 100%;
    height: 42px;
    margin-top: 39px;
    background: linear-gradient(90deg,#ecb53c,#ff9211);
    color: #fff;
    border-radius: 39px;
    box-shadow: 0 10px 20px 0 rgba(0,0,0,.1);
    letter-spacing: 2px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
}
</style>
